import React from 'react';
import  {Link} from 'react-router-dom'

const photoStyle = {
  display: 'inline-block',
  margin: '0 0 1em',
  width: '100%'
}

const linkStyle = {
  textAlign: 'center',
  display: 'block',
  marginBottom: '20px',
  fontSize: '26px'
}

const imgStyle={
  width: '100%'
}

function Photo(props) {
  // return <Link to={`/stock-photo/photo/${props.id}`}>
  //     <img className="photo" style={photoStyle} id={props.id} src={props.src}></img>
  //     <a href={props.download}>Жми чтобы скачать</a>
  //   </Link>
 return  <div style={photoStyle}>
   <img className="photo" style={imgStyle} id={props.id} src={props.src}></img>
  <a style={linkStyle} href={props.download} download target='_blank'>Жми чтобы скачать</a>
  </div>

}

export default Photo;
